@import url(https://fonts.googleapis.com/css2?family=Arvo:wght@400;700&display=swap);
body {
  display: flex;
  text-align: center;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{box-sizing:border-box;font-family:'Arvo', serif;align-content:center;overflow-x:hidden;justify-items:center}img{margin:10px;cursor:pointer}h1{background-color:royalblue;color:white;font-size:4rem}.form{display:flex;align-items:flex-end;justify-content:center}.form button{text-decoration:none;margin:2rem;width:8rem;height:3rem;background-color:#fff;color:royalblue;border:solid 3px royalblue;font-size:1rem;border-radius:10px;font-weight:600;text-transform:uppercase}.form button:hover{background-color:royalblue;color:white}.form button:active{opacity:.5;background-color:royalblue;color:white}.form .wrapper{display:flex;flex-direction:column}.form .wrapper input{font-size:15px;margin:10px}.form .wrapper .btn{display:flex;flex-direction:row}

